import React from 'react';
import Blog from './Blog/Blog';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';

const App: React.FC = () => {
  return (
    <div>
      <Router>
        <ul>
          <li><Link to="/home">Home</Link></li>
          <li><Link to="/blog">blog</Link></li>
        </ul>
        <Switch>
          <Route path="/home">
            <h2>Matti Leinonen</h2>
          </Route>
          <Route path="/blog">
            <Blog />
          </Route>
        </Switch>
      </Router>
    </div>
  );
};

export default App;